<template>
  <div>
    <div class="info">
      <el-table :data="tableData" @row-click="gotoDe" style="width: 100%">
        <el-table-column label="站点" prop="name"></el-table-column>
      </el-table>
    </div>
    <div class="map">
      <baidu-map
        :center="center"
        :scroll-wheel-zoom="true"
        :zoom="zoom"
        @rightclick="rightclick"
        class="bm-view"
      >
        <bm-control :offset="{width: '10px', height: '10px'}">
          <bm-auto-complete :sugStyle="{zIndex: 1}" v-model="keyword"></bm-auto-complete>
        </bm-control>
        <bm-local-search :auto-viewport="true" :keyword="keyword"></bm-local-search>
        <bm-navigation anchor="BMAP_ANCHOR_TOP_RIGHT"></bm-navigation>
        <bm-context-menu>
          <bm-context-menu-item :callback="addP" text="添加到站点"></bm-context-menu-item>
        </bm-context-menu>
        <bm-marker
          :dragging="true"
          :position="{lng: 120.317, lat: 30.239}"
          animation="BMAP_ANIMATION_BOUNCE"
        >
          <bm-label
            :labelStyle="{color: 'red', fontSize : '10px'}"
            :offset="{width: 0, height: 30}"
            content="萧山站"
          />
        </bm-marker>
      </baidu-map>
    </div>
  </div>
</template>
<script>
export default {
  data () {
    return {
      zoom: 12,
      keyword: '',
      // 经度
      lng: '',
      // 纬度
      lat: '',
      center: '杭州',
      tableData: [{
        name: '萧山',
        lng: 120.317,
        lat: 30.239
      }]
    }
  },
  methods: {
    gotoDe (row) {
      console.log(row);
      this.center = { lng: row.lng, lat: row.lat };
      // this.center = { lng: 116.404, lat: 39.915 };
      this.zoom = 15;
    },
    rightclick (e) {
      console.log(e);

      this.lng = e.point.lng;
      this.lat = e.point.lat;
    },
    addP () {
      alert(this.lan + ',' + this.lat);
    }
  }
}
</script>


<style>
.info {
  display: inline-block;
  width: 18%;
  height: 800px;
}
.map {
  width: 80%;
  height: 800px;
  float: right;
}
.bm-view {
  width: 100%;
  height: 800px;
}
</style>


